import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FirsComponent } from './firs/firs.component';
import { ListdetailComponent } from './todo/listdetail/listdetail.component';
import { TodoInputComponent } from './todo/todo-input/todo-input.component';

const routes: Routes = [
 
  {path:'table',component:ListdetailComponent},
  {path:'todolist',component:FirsComponent},
  {path:'input',component:TodoInputComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
